import { Component, OnInit } from '@angular/core';
import { CommonService } from './../../services/common.service';
import { Router, ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-update-service',
  templateUrl: './update-service.component.html',
  styleUrls: ['./update-service.component.scss']
})
export class UpdateServiceComponent implements OnInit {
  id: any;
  service: any = {};
  message: string;
  showMessage: boolean = false;
  isSuccess: boolean = false;
  constructor(private commonSvc: CommonService, public activeRouter: ActivatedRoute, public router: Router) {}

  ngOnInit() {
    this.activeRouter.paramMap.subscribe(params => {
      this.id = params.get("id");
      this.getService();
    });
  }

  getService(){
    var that = this;
    this.commonSvc.getService(this.id).subscribe((res:any)=>{
      that.service = res;
  });
  }

  update(form) {
    var that = this;
    if (form.valid) {
      console.log(form.value);
      this.commonSvc.updateService(form.value, this.id).subscribe(
        (res: any) => {
          this.message = "Update service Successfully!";
          this.showMessage = true;
          this.isSuccess = true;
          this.router.navigate(["/dashboard/services"]);
        },
        (error: any) => {
          if (error.status == 422) {
            this.message =
            "Please fill required fields!";
          this.showMessage = true;
          this.isSuccess = false;
          } else {
            this.message = "Update service Fail!";
            this.showMessage = true;
            this.isSuccess = false;
          }
        }
      );
    } else {
      this.message =
        "Please fill required fields!";
      this.showMessage = true;
      this.isSuccess = false;
    }
  }

}
